<template>
    <div class="hello">
        <table class="uk-table uk-table-divider uk-table-striped">
            <caption></caption>
            <thead>
                <tr>
                    <td>
                        name
                    </td>
                    <td>
                        email
                    </td>
                    <td>
                        role
                    </td>
                </tr>
            </thead>
            <tbody>
                 <tr>
                    <td>
                        {{user.name}}
                    </td>
                    <td>
                        {{user.email}}
                    </td>
                    <td>
                        {{user.role}}
                    </td>
                </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
    export default {
        name: "ProfilComponent",
        data () {
            return {
                user: JSON.parse(localStorage.getItem('user')),
                loading: false
            }
        },
        beforeCreate(){
            
        }
    }
</script>

<style scoped>
    li{
        width: 100vw;
        display: flex;
        justify-content: space-around;
    }
    thead tr{
        font-weight: bold;
        text-transform: capitalize;
    }
    .uk-table-striped tbody tr:nth-of-type(odd){
        background: #4f5861;
    }
</style>


